import React, { useEffect, useState, useRef } from "react";

import Button from "react-bootstrap/Button";

import Web3 from "web3";
import "@metamask/legacy-web3";
import Web3Utils from "web3-utils";

import config from "../../../lib/config";
import MINT_ABI from "../../../ABI/singlemint.json";

import { toast } from "react-toastify";

import { getReceipt } from "../../../actions/tokenactions";
import { ContactSupportOutlined } from "@material-ui/icons";

toast.configure();
let toasterOption = config.toasterOption;
const MintContract = config.SINGLE_CONTRACT;

var web3 = new Web3(window.ethereum);

const APESMARTCONTRACT = new web3.eth.Contract(MINT_ABI, MintContract);

const MINTComponent = (props) => {
  const { ...rest } = props;

  const [Account, set_Accounts] = useState("");
  const [Balance, set_Balance] = useState(0);
  const [TokenPrice, set_TokenPrice] = useState(0);
  const [TokenMax, set_TokenMax] = useState(0);
  const [Buyquantity, set_Buyquantity] = useState(1);
  const [TotalAmount, set_TotalAmount] = useState(0);
  const [ConnectionStatus, set_ConnectionStatus] = useState(false);
  const [Loader, set_Loader] = useState(false);

  useEffect(() => {
    CheckConnected();
    GetContractData();
  }, []);

  async function GetContractData() {
    var tokenvalue = await APESMARTCONTRACT.methods.PotbPrice().call();
    set_TokenPrice(tokenvalue / config.decimalvalues);
    var inittotal = (tokenvalue / config.decimalvalues) * 1;
    set_TotalAmount(inittotal);
    var tokenmax = await APESMARTCONTRACT.methods.maxPotbPurchase().call();
    set_TokenMax(tokenmax);
  }
  async function CheckConnected() {
    if (window.ethereum) {
      try {
        const web3 = await new Web3(window.ethereum);
        if (typeof web3 !== "undefined") {
          if (window.web3.currentProvider.isMetaMask === true) {
            if (window.web3.currentProvider.networkVersion != null) {
              if (
                window.web3.currentProvider.networkVersion ===
                config.networkVersion
              ) {
                await web3.eth.getAccounts(async function (error, result) {
                  if (result) {
                    set_Accounts(result[0]);
                    set_ConnectionStatus(true);
                    var balance = await web3.eth.getBalance(result[0]);
                    set_Balance(balance / config.decimalvalues);
                  }
                });
              } else {
                toast.error(
                  "Please Connect  to " + config.PrefferedNetwork,
                  toasterOption
                );
              }
            }
          }
        }
      } catch (err) {}
    }
  }

  async function connectMetamask() {
    if (window.ethereum) {
      var web3 = new Web3(window.ethereum);
      try {
        if (typeof web3 !== "undefined") {
          window.ethereum.enable().then(async function () {
            const web3 = new Web3(window.web3.currentProvider);
            if (window.web3.currentProvider.isMetaMask === true) {
              await web3.eth.getAccounts(async function (error, result) {
                set_Accounts(result[0]);
                set_ConnectionStatus(true);
                var balance = await web3.eth.getBalance(result[0]);
                set_Balance(balance / config.decimalvalues);
              });
            } else {
              toast.error("Please Add Metamask External", toasterOption);
            }
          });
        } else {
          toast.error("Please Add Metamask External", toasterOption);
        }
      } catch (err) {}
    }
  }

  window.addEventListener("load", (event) => {
    event.preventDefault();
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", async function (accounts) {
        window.location.reload(false);
        window.ethereum.enable().then(async function () {
          const web3 = await new Web3(window.web3.currentProvider);
          await web3.eth.getAccounts(async function (error, result) {
            set_Accounts(result[0]);
            set_ConnectionStatus(true);
            var balance = await web3.eth.getBalance(result[0]);
            set_Balance(balance / config.decimalvalues);
          });
        });
      });

      window.ethereum.on("networkChanged", function (networkId) {
        console.log("Networkdddid ", typeof networkId);
        if (networkId === config.networkVersion) {
          //   window.location.reload(false);
        } else {
          toast.error(
            "Please Connect to " + config.PrefferedNetwork,
            toasterOption
          );
        }
      });
    }
  });

  async function UpdateQty(type) {
    //   alert(type)
    if (type === "plus") {
      var newquantity = Buyquantity + 1;
      if (newquantity <= TokenMax) {
        set_Buyquantity(newquantity);
        var finalamount = newquantity * TokenPrice;
        set_TotalAmount(finalamount.toFixed(3));
      }
    } else {
      var checnewquantity = Buyquantity - 1;
      var setqty = checnewquantity >= 0 ? checnewquantity : 0;
      set_Buyquantity(setqty);
      var finalamountnew = setqty * TokenPrice;
      set_TotalAmount(finalamountnew);
    }
  }

  async function MintCall() {
    if (!window.ethereum) {
      toast.error("Oops! Please Connect your wallet", toasterOption);
      return false;
    }

    var mintCall = null;
    var receipt = null;
    var handle = null;
    var web3 = new Web3(window.ethereum);
    var CurrAddr = window.web3.eth.defaultAccount;
    if (!CurrAddr) {
      toast.warning("OOPS!..connect Your Wallet", toasterOption);
      return false;
    }

    var ContractCall = await APESMARTCONTRACT.methods.mintPotb(Buyquantity)
    try{
      set_Loader(true)
        await ContractCall.send({from:CurrAddr,value:web3.utils.toWei(TotalAmount.toString()) })
        .on('transactionHash',(transactionHash)=>{
            mintCall=transactionHash
            if(mintCall){
                handle = setInterval(async () => {
                    receipt = await getReceipt(web3, transactionHash)
                    ////console.log("receipt", receipt)
                    clrtrns();
                  }, 2000)}  
            })
        }
    
    catch(err){
      set_Loader(false)

        console.log("errreerere",err)
        toast.error("Mint Not Successfull", toasterOption);
    }

    async function clrtrns(){
        if (receipt!=null) {
            clearInterval(handle);
            set_Loader(false)

              if (receipt.status === true) {
                var transactionHash = mintCall;
                console.log("transactionHashtransactionHash",transactionHash)
                toast.success("Your NFT created successfully", toasterOption);
  
              }
        }
    }
   

  }

  return (
    <div id="mint">
      <div className="px-3 px-md-5 py-5 position-relative h-100 mb-1">
        <div className="row">
          <div className="col-lg-6 mb-4 mb-lg-0">
            <img
              src={require("../../../assets/img/gallery/main.gif").default}
              className="img-fluid round-custom h-100"
              alt="#"
            />
          </div>
          <div className="col-lg-6">
            <div>
              <div className="claim-text-wrapper">
                <div className="payment-modal">
                  <div className="payment-header">
                    <div className="payment-header-text">
                      <h4>CLAIM YOUR NFT</h4>
                      <p>Enter how many tokens you would like to mint here</p>
                    </div>
                    <img
                      className="payment-header-img"
                      src={require("../../../assets/img/logo.png").default}
                      alt="Angry Apes United Logo"
                    />
                  </div>
                  <div className="payment-info">
                    <img
                      src={
                        require("../../../assets/img/gallery/main.gif").default
                      }
                      className="img-fluid price-img"
                      alt="Claim Ape NFT"
                    />
                    <div className="payment-info-text">
                      <p>Price Per NFT</p>
                      <h5>
                        {TokenPrice} {config.currencySymbol} Each
                      </h5>
                      {/* <p>Public sale available 11.10.2021</p> */}
                    </div>
                  </div>
                  <div className="ape-number">
                    <div className="minus" onClick={() => UpdateQty("minus")}>
                      <img
                        src={
                          require("../../../assets/img/icons/minus.svg").default
                        }
                        alt="#"
                      />
                    </div>
                    <h5>{Buyquantity}</h5>
                    <div className="plus" onClick={() => UpdateQty("plus")}>
                      <img
                        src={
                          require("../../../assets/img/icons/plus.svg").default
                        }
                        alt="#"
                      />
                    </div>
                    <h5 className="ape-max">{TokenMax} Max</h5>
                  </div>
                  <div className="ape-total">
                    <p>Total</p>
                    <h5>
                      {TotalAmount} {config.currencySymbol}
                    </h5>
                  </div>
                </div>

                {!ConnectionStatus && (
                  <Button
                    onClick={connectMetamask}
                    className="w-100 round-custom purchase-button-wrapper"
                    size="lg"
                  >
                    CONNECT WALLET
                  </Button>
                )}
                {ConnectionStatus && (
                  <Button
                    onClick={MintCall}
                    className="w-100 round-custom purchase-button-wrapper"
                    size="lg"
                    disabled={Loader}
                  >
                    MINT
                  </Button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MINTComponent;
